.ability {
    display: block;
    font-family: Helvetica;
    color: #FFF;
    margin: 5px auto;
    text-transform: capitalize;
    max-width: 400px;
}

.ability_button {
    background: rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    border: 1px solid #fff;
    color: #FFF;
    cursor: pointer; 
    display: block;
    font-family: Helvetica;
    padding: 10px;
    text-transform: capitalize;
    width: 100%;
    position: relative;
}

.ability_type {
    display: inline-block;
    border-radius: 3px;
    background: rgba(0,0,0, 0.5);
    padding: 3px;
    font-size: 10px;
    text-shadow: 1px 1px 1px #000;
    margin-left: 10px;
    position: absolute;
    right: 10px;
    top: 9px;
    box-shadow: 1px 1px 1px #0000003d;
}
