.Card {
    border: 0;
    border-radius: 50%;
    display: inline-block;
    font-family: Helvetica;
    height: 200px;
    margin: 10px;
    padding: 10px;
    width: 200px;
    vertical-align: top;
    position: relative;
    cursor: pointer;
    overflow: visible;
}

.Card:before {
    background: #33353c;
    border-radius: 50%;
    content: '';
    display: block;
    position: absolute;
    top: 3px;
    right: 3px;
    bottom: 3px;
    left: 3px;
    z-index: 0;
    transition: all 150ms ease-in;
}

.Card_id {
    background:#33353c;
    border: 2px solid #fff;
    border-radius: 50%;
    color: #fff;
    padding: 7px 0 0 0;
    display: block;
    text-align: center;
    position: absolute;
    z-index: 20;
    width: 30px;
    height: 30px;
    font-size: 12px;
    top: 15px;
    left: 15px;
}

.Card_name {
    color: #fff;
    text-transform: capitalize;
    display: block;
    font-size: 20px;
    position: relative;
    z-index: 20;
    text-align: center;
    padding-top: 13px;
    font-size: 14px;
}

.Card_image {
    position: relative;
    z-index: 20;
    width: 100px;
    height: 100px;
    display: block;
    margin: 0 auto;
}

.Card_types {
    color: #fff;
    text-transform: capitalize;
    position: relative;
    z-index: 20;
}

.Card_bg_name {
    z-index: 10;
    font-size: 180px;
    color: #2f2e2e;
    position: absolute;
    text-transform: capitalize;
    -moz-transform:translateX(12%);
    -webkit-transform:translateX(12%);	
    transform:translateX(12%);
}

.Card:hover .Card_bg_name {
    -moz-animation: marquee 2s linear infinite;
    -webkit-animation: marquee 2s linear infinite;
    animation: marquee 2s linear infinite;
}

.Card:hover .Card_bg_container{
    top: 10px;
    right: 10px;
    bottom: 10px;
    left: 10px;
}

.Card:hover::before{
    top: 10px;
    right: 10px;
    bottom: 10px;
    left: 10px;
}

@-moz-keyframes marquee {
    0%   { -moz-transform: translateX(12%); }
    100% { -moz-transform: translateX(-100%); }
}
@-webkit-keyframes marquee {
    0%   { -webkit-transform: translateX(12%); }
    100% { -webkit-transform: translateX(-100%); }
}
@keyframes marquee {
    0% {
        -moz-transform: translateX(12%);
        -webkit-transform: translateX(12%);
        transform: translateX(12%);
    }
    100% { 
        -moz-transform: translateX(-100%);
        -webkit-transform: translateX(-100%);
        transform: translateX(-100%); 
    }
}

.Card_types_list {
    display: inline-block;
    padding: 5px;
    border-radius: 5px;
    min-width: 65px;
    font-size: 14px;
    text-shadow: 1px 1px 1px #000;
}

.Card_bg_container {
    position: absolute;
    border-radius: 50%;
    overflow: hidden;
    top: 3px;
    right: 3px;
    bottom: 3px;
    left: 3px;
    transition: all 150ms ease-in;
}

.Card_currStat {
    position: absolute;
    z-index: 20;
    color: #fff;
    border: 2px solid #FFF;
    padding: 5px;
    border-radius: 50%;
    display: block;
    height: 50px;
    width: 50px;
    background: #33353c;
    top: 0;
    right: 0;
    font-size: 20px;
}

.Card_currStat:before {
    content: '';
    display: block;
    width: 16px;
    height: 16px;
    background-size: contain;
    margin: -2px auto 3px;
}

.Card_currStat.speed:before {
    background-image: url('./speed-white.svg');
}
.Card_currStat.sdef:before {
    background-image: url('./specialDef-white.svg');
}
.Card_currStat.satt:before {
    background-image: url('./specialAtt-white.svg');
}
.Card_currStat.def:before {
    background-image: url('./defense-white.svg');
}
.Card_currStat.att:before {
    background-image: url('./attack-white.svg');
}
.Card_currStat.hp:before {
    background-image: url('./heart-white.svg');
}