.ability_stats {
    display: block;
    position: absolute;
    top: 5px;
    left: 5px;
    text-transform: capitalize;
}

.ability_stats-acc {
    border-radius: 4px;
    display: inline-block;
    font-size: 11px;
    margin-right: 5px;
    padding: 4px;
    background: rgba(0, 0, 0, 0.21);
}

.ability_stats-acc:before {
    background: url('./target.svg') center no-repeat;
    background-size: contain;
    content: '';
    display: inline-block;
    height: 15px;
    margin-right: 2px;
    width: 15px;
}

.ability_stats-dmg {
    border-radius: 4px;
    display: inline-block;
    font-size: 11px;
    margin-right: 5px;
    padding: 4px;
    background: rgba(0, 0, 0, 0.21);
}

.ability_stats-dmg:before {
    background: url('./sword.svg') center no-repeat;
    background-size: contain;
    content: '';
    display: inline-block;
    height: 15px;
    margin-right: 2px;
    width: 15px;
}

.ability_name {
    display: inline-block;
    text-transform: capitalize;
    font-size: 14px;
    text-shadow: rgba(0, 0, 0, 0.65);
}

.ability_description {
    padding: 10px;
    background: #fff;
    color: #000;
    font-size: 13px;
    text-align: left;
    line-height: 1.5;
    text-transform: none;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.ability_button.display {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0; 
}