/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
* {
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width:9px;
}

::-webkit-scrollbar-track {
  border-radius:5px;
  background:rgba(0,0,0,0.1);
}

::-webkit-scrollbar-thumb {
  border-radius:5px;
  background:rgba(0,0,0,0.2);
}

::-webkit-scrollbar-thumb:hover {
  background:rgba(0,0,0,0.4);
}

::-webkit-scrollbar-thumb:window-inactive {
  background:rgba(0,0,0,0.05);
}

.Card {
    border: 0;
    border-radius: 50%;
    display: inline-block;
    font-family: Helvetica;
    height: 200px;
    margin: 10px;
    padding: 10px;
    width: 200px;
    vertical-align: top;
    position: relative;
    cursor: pointer;
    overflow: visible;
}

.Card:before {
    background: #33353c;
    border-radius: 50%;
    content: '';
    display: block;
    position: absolute;
    top: 3px;
    right: 3px;
    bottom: 3px;
    left: 3px;
    z-index: 0;
    transition: all 150ms ease-in;
}

.Card_id {
    background:#33353c;
    border: 2px solid #fff;
    border-radius: 50%;
    color: #fff;
    padding: 7px 0 0 0;
    display: block;
    text-align: center;
    position: absolute;
    z-index: 20;
    width: 30px;
    height: 30px;
    font-size: 12px;
    top: 15px;
    left: 15px;
}

.Card_name {
    color: #fff;
    text-transform: capitalize;
    display: block;
    font-size: 20px;
    position: relative;
    z-index: 20;
    text-align: center;
    padding-top: 13px;
    font-size: 14px;
}

.Card_image {
    position: relative;
    z-index: 20;
    width: 100px;
    height: 100px;
    display: block;
    margin: 0 auto;
}

.Card_types {
    color: #fff;
    text-transform: capitalize;
    position: relative;
    z-index: 20;
}

.Card_bg_name {
    z-index: 10;
    font-size: 180px;
    color: #2f2e2e;
    position: absolute;
    text-transform: capitalize;
    -webkit-transform:translateX(12%);	
    transform:translateX(12%);
}

.Card:hover .Card_bg_name {
    -webkit-animation: marquee 2s linear infinite;
    animation: marquee 2s linear infinite;
}

.Card:hover .Card_bg_container{
    top: 10px;
    right: 10px;
    bottom: 10px;
    left: 10px;
}

.Card:hover::before{
    top: 10px;
    right: 10px;
    bottom: 10px;
    left: 10px;
}
@-webkit-keyframes marquee {
    0%   { -webkit-transform: translateX(12%); }
    100% { -webkit-transform: translateX(-100%); }
}
@keyframes marquee {
    0% {
        -webkit-transform: translateX(12%);
        transform: translateX(12%);
    }
    100% {
        -webkit-transform: translateX(-100%);
        transform: translateX(-100%); 
    }
}

.Card_types_list {
    display: inline-block;
    padding: 5px;
    border-radius: 5px;
    min-width: 65px;
    font-size: 14px;
    text-shadow: 1px 1px 1px #000;
}

.Card_bg_container {
    position: absolute;
    border-radius: 50%;
    overflow: hidden;
    top: 3px;
    right: 3px;
    bottom: 3px;
    left: 3px;
    transition: all 150ms ease-in;
}

.Card_currStat {
    position: absolute;
    z-index: 20;
    color: #fff;
    border: 2px solid #FFF;
    padding: 5px;
    border-radius: 50%;
    display: block;
    height: 50px;
    width: 50px;
    background: #33353c;
    top: 0;
    right: 0;
    font-size: 20px;
}

.Card_currStat:before {
    content: '';
    display: block;
    width: 16px;
    height: 16px;
    background-size: contain;
    margin: -2px auto 3px;
}

.Card_currStat.speed:before {
    background-image: url(/static/media/speed-white.1886c77f.svg);
}
.Card_currStat.sdef:before {
    background-image: url(/static/media/specialDef-white.c7bb792b.svg);
}
.Card_currStat.satt:before {
    background-image: url(/static/media/specialAtt-white.d5798cfe.svg);
}
.Card_currStat.def:before {
    background-image: url(/static/media/defense-white.cf2be3a3.svg);
}
.Card_currStat.att:before {
    background-image: url(/static/media/attack-white.884e1409.svg);
}
.Card_currStat.hp:before {
    background-image: url(/static/media/heart-white.ca546af0.svg);
}
.lightBox {
    opacity: 0;
    visibility: hidden; 
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(20, 20, 20, 0.8);
    transition: 0.5s;
    z-index: 30;
}

.lightBox.show {
    opacity: 1;
    visibility: visible; 
}

.lightBox_close {
    background: none;
    border-radius: 50%;
    border: 2px solid white;
    cursor: pointer;
    color: white;
    position: absolute;
    top: 15px;
    right: 15px;
    width: 50px;
    height: 50px;
    text-indent: -9999px;
    overflow: hidden;
}

.lightBox_close:before {
    content: '';
    position: absolute;
    display: block;
    height: 46px;
    width: 46px;
    top: 0;
    left: 0;
    background: url(/static/media/cancel.585b265f.svg) no-repeat center;
    background-size: 25px 25px;
}

.lightBox_content {
    background: #191a1c;
    display: block;
    position: absolute;
    top: 60px;
    left: 60px;
    right: 60px;
    bottom: 60px;
    border-radius: 10px;
    overflow: hidden;
}

.lightBox_image {
    display: block;
    position: relative;
    height: 200px;
    width: 200px;
    z-index: 20;
}

.lightBox_imageContainer {
    display: block;
    margin: 0 auto;
    position: relative;
    width: 200px;
    height: 200px;
}

.lightBox_name {
    color: #fff;
    font-family: Helvetica;
    font-size: 30px;
    background: rgba(0, 0, 0, 0.2);
    padding: 8px 0 10px;
    text-transform: capitalize;
    text-shadow: 1px 1px 1px #000;
    position: relative;
    z-index: 10;
}

.lightBox_info {
    background: #3a3c4a;
    position: absolute;
    padding: 0 15px 20px;
    top: 248px;
    left: 0px;
    right: 0;
    bottom: 20px;
    z-index: 20;
    overflow: auto;
}

.lightBox_types {
    color: #fff;
    font-family: Helvetica;
    text-transform: capitalize;
    position: relative;
    z-index: 20;
}

.lightBox_types_list {
    display: inline-block;
    padding: 10px;
    border-radius: 5px;
    min-width: 65px;
    font-size: 20px;
    text-shadow: 1px 1px 1px #000;
    margin: 10px 2px;
}

.lightBox_heading {
    color: #FFF;
    font-family: Helvetica;
    font-size: 25px;
    margin: 24px 0 10px;
}

.lightBox_imageContainer_name1 {
    position: absolute;
    font-family: Helvetica;
    font-size: 294px;
    text-transform: capitalize;
    z-index: 10;
    left: 0;
