.lightBox_prev, 
.lightBox_next {
    background: rgba(0, 0, 0, 0.2);
    border: 2px solid #FFF;
    color: #FFF;
    position: absolute;
    z-index: 20;
    width: 50px;
    height: 50px;
    display: block;
    border-radius: 50%;
    overflow: hidden;
    text-indent: -9999px;
}

.lightBox_prev:before {
    content: '';
    position: absolute;
    display: block;
    height: 46px;
    width: 46px;
    top: 0;
    left: -2px;
    background: url(/static/media/back.184e2b4e.svg) no-repeat center;
    background-size: 30px 30px;
}
.lightBox_next:before {
    content: '';
    position: absolute;
    display: block;
    height: 46px;
    width: 46px;
    top: 0;
    right: -2px;
    background: url(/static/media/next.e692a03e.svg) no-repeat center;
    background-size: 30px 30px;
}

.lightBox_prev {
    top: 50%;
    left: 10px;
    margin-top: -25px;
}

.lightBox_next {
    top: 50%;
    right: 10px;
    margin-top: -25px;
}

