.ability {
    display: block;
    font-family: Helvetica;
    color: #FFF;
    margin: 5px auto;
    text-transform: capitalize;
    max-width: 400px;
}

.ability_button {
    background: rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    border: 1px solid #fff;
    color: #FFF;
    cursor: pointer; 
    display: block;
    font-family: Helvetica;
    padding: 10px;
    text-transform: capitalize;
    width: 100%;
    position: relative;
}

.ability_type {
    display: inline-block;
    border-radius: 3px;
    background: rgba(0,0,0, 0.5);
    padding: 3px;
    font-size: 10px;
    text-shadow: 1px 1px 1px #000;
    margin-left: 10px;
    position: absolute;
    right: 10px;
    top: 9px;
    box-shadow: 1px 1px 1px #0000003d;
}

.ability_stats {
    display: block;
    position: absolute;
    top: 5px;
    left: 5px;
    text-transform: capitalize;
}

.ability_stats-acc {
    border-radius: 4px;
    display: inline-block;
    font-size: 11px;
    margin-right: 5px;
    padding: 4px;
    background: rgba(0, 0, 0, 0.21);
}

.ability_stats-acc:before {
    background: url(/static/media/target.7f965126.svg) center no-repeat;
    background-size: contain;
    content: '';
    display: inline-block;
    height: 15px;
    margin-right: 2px;
    width: 15px;
}

.ability_stats-dmg {
    border-radius: 4px;
    display: inline-block;
    font-size: 11px;
    margin-right: 5px;
    padding: 4px;
    background: rgba(0, 0, 0, 0.21);
}

.ability_stats-dmg:before {
    background: url(/static/media/sword.404ae71a.svg) center no-repeat;
    background-size: contain;
    content: '';
    display: inline-block;
    height: 15px;
    margin-right: 2px;
    width: 15px;
}

.ability_name {
    display: inline-block;
    text-transform: capitalize;
    font-size: 14px;
    text-shadow: rgba(0, 0, 0, 0.65);
}

.ability_description {
    padding: 10px;
    background: #fff;
    color: #000;
    font-size: 13px;
    text-align: left;
    line-height: 1.5;
    text-transform: none;
