    border-bottom-right-radius: 5px;
}

.ability_button.display {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0; 
}
.menu {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 30;
    background: #252629ad;
}

.menu-list-item {
    display: inline-block;
    margin: 5px 8px 5px 0;
}

.menu-list-button {
    display: block;
    cursor: pointer;
    border: 0 none;
    background: none;
    color: #fff;
    width: 60px;
    height: 50px;
    position: relative;
    text-indent: -9999px;
    overflow: hidden;
}

.menu-list-button:before {
    content: '';
    display: block;
    width: 40px;
    height: 40px;
    position: absolute;
    top: 5px;
    left: 50%;
    margin-left: -20px;
}

.menu-list-button {
    background: url(/static/media/all.1f04ac21.svg) center no-repeat;
    background-size: contain;
}

.menu-list-button.speed {
    background: url(/static/media/speed-white.1886c77f.svg) center no-repeat;
    background-size: contain;
}

.menu-list-button.sDef {
    background: url(/static/media/specialDef-white.c7bb792b.svg) center no-repeat;
    background-size: contain;
}

.menu-list-button.sAtt {
    background: url(/static/media/specialAtt-white.d5798cfe.svg) center no-repeat;
    background-size: contain;
}

.menu-list-button.def {
    background: url(/static/media/defense-white.cf2be3a3.svg) center no-repeat;
    background-size: contain;
}

.menu-list-button.att {
    background: url(/static/media/attack-white.884e1409.svg) center no-repeat;
    background-size: contain;
}

.menu-list-button.hp {
    background: url(/static/media/heart-white.ca546af0.svg) center no-repeat;
    background-size: contain;
}

.menu-list-button.type {
    background: url(/static/media/type.3cbad6cb.svg) center no-repeat;
    background-size: contain;
}

.menu-list-button.asc:after {
    content: '';
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    border: 5px solid transparent;
    border-bottom: 9px solid #FFF;
    right: 0;
    bottom: 0;
}

.menu-list-button.des:after {
    content: '';
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    border: 5px solid transparent;
    border-top: 9px solid #FFF;
    right: 0;
    bottom: -5px;
}

