.lightBox {
    opacity: 0;
    visibility: hidden; 
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(20, 20, 20, 0.8);
    transition: 0.5s;
    z-index: 30;
}

.lightBox.show {
    opacity: 1;
    visibility: visible; 
}

.lightBox_close {
    background: none;
    border-radius: 50%;
    border: 2px solid white;
    cursor: pointer;
    color: white;
    position: absolute;
    top: 15px;
    right: 15px;
    width: 50px;
    height: 50px;
    text-indent: -9999px;
    overflow: hidden;
}

.lightBox_close:before {
    content: '';
    position: absolute;
    display: block;
    height: 46px;
    width: 46px;
    top: 0;
    left: 0;
    background: url('./cancel.svg') no-repeat center;
    background-size: 25px 25px;
}

.lightBox_content {
    background: #191a1c;
    display: block;
    position: absolute;
    top: 60px;
    left: 60px;
    right: 60px;
    bottom: 60px;
    border-radius: 10px;
    overflow: hidden;
}

.lightBox_image {
    display: block;
    position: relative;
    height: 200px;
    width: 200px;
    z-index: 20;
}

.lightBox_imageContainer {
    display: block;
    margin: 0 auto;
    position: relative;
    width: 200px;
    height: 200px;
}

.lightBox_name {
    color: #fff;
    font-family: Helvetica;
    font-size: 30px;
    background: rgba(0, 0, 0, 0.2);
    padding: 8px 0 10px;
    text-transform: capitalize;
    text-shadow: 1px 1px 1px #000;
    position: relative;
    z-index: 10;
}

.lightBox_info {
    background: #3a3c4a;
    position: absolute;
    padding: 0 15px 20px;
    top: 248px;
    left: 0px;
    right: 0;
    bottom: 20px;
    z-index: 20;
    overflow: auto;
}

.lightBox_types {
    color: #fff;
    font-family: Helvetica;
    text-transform: capitalize;
    position: relative;
    z-index: 20;
}

.lightBox_types_list {
    display: inline-block;
    padding: 10px;
    border-radius: 5px;
    min-width: 65px;
    font-size: 20px;
    text-shadow: 1px 1px 1px #000;
    margin: 10px 2px;
}

.lightBox_heading {
    color: #FFF;
    font-family: Helvetica;
    font-size: 25px;
    margin: 24px 0 10px;
}

.lightBox_imageContainer_name1 {
    position: absolute;
    font-family: Helvetica;
    font-size: 294px;
    text-transform: capitalize;
    z-index: 10;
    left: 0;
    right: 0;
    -moz-animation: marqueeLightBox 10s linear infinite;
    -webkit-animation: marqueeLightBox 10s linear infinite;
    animation: marqueeLightBox 10s linear infinite;
}

.lightBox_imageContainer_name2 {
