    left: 0;
    right: 0;
    -moz-animation: marqueeLightBox 8s linear infinite;
    -webkit-animation: marqueeLightBox 8s linear infinite;
    animation: marqueeLightBox 8s linear infinite;
}

.lightBox_imageContainer_name3 {
