    z-index: 10;
    left: 0;
    right: 0;
    -moz-animation: marqueeLightBox 5s linear infinite;
    -webkit-animation: marqueeLightBox 5s linear infinite;
    animation: marqueeLightBox 5s linear infinite;
}

.lightBox_prev, 
.lightBox_next {
    background: rgba(0, 0, 0, 0.2);
    border: 2px solid #FFF;
    color: #FFF;
    position: absolute;
    z-index: 20;
    width: 50px;
    height: 50px;
    display: block;
    border-radius: 50%;
    overflow: hidden;
    text-indent: -9999px;
}

.lightBox_prev:before {
    content: '';
    position: absolute;
    display: block;
    height: 46px;
    width: 46px;
    top: 0;
    left: -2px;
    background: url('./back.svg') no-repeat center;
    background-size: 30px 30px;
}
.lightBox_next:before {
    content: '';
    position: absolute;
    display: block;
    height: 46px;
    width: 46px;
    top: 0;
    right: -2px;
    background: url('./next.svg') no-repeat center;
    background-size: 30px 30px;
}

.lightBox_prev {
    top: 50%;
    left: 10px;
    margin-top: -25px;
}

.lightBox_next {
    top: 50%;
    right: 10px;
    margin-top: -25px;
}

.lightBox_stats-item {
    background: #fff;
    border-radius: 50%;
    display: inline-block;
    color: #3a3c4a;
    font-family: Helvetica;
    height: 50px;
    width: 50px;
    margin: 0 4px;
    padding-top: 25px;
    font-size: 19px;
    position: relative;
    overflow: visible;
}

.lightBox_stats-item.speed:before {
