.menu {
    position: sticky;
    top: 0;
    z-index: 30;
    background: #252629ad;
}

.menu-list-item {
    display: inline-block;
    margin: 5px 8px 5px 0;
}

.menu-list-button {
    display: block;
    cursor: pointer;
    border: 0 none;
    background: none;
    color: #fff;
    width: 60px;
    height: 50px;
    position: relative;
    text-indent: -9999px;
    overflow: hidden;
}

.menu-list-button:before {
    content: '';
    display: block;
    width: 40px;
    height: 40px;
    position: absolute;
    top: 5px;
    left: 50%;
    margin-left: -20px;
}

.menu-list-button {
    background: url('./all.svg') center no-repeat;
    background-size: contain;
}

.menu-list-button.speed {
    background: url('./speed-white.svg') center no-repeat;
    background-size: contain;
}

.menu-list-button.sDef {
    background: url('./specialDef-white.svg') center no-repeat;
    background-size: contain;
}

.menu-list-button.sAtt {
    background: url('./specialAtt-white.svg') center no-repeat;
    background-size: contain;
}

.menu-list-button.def {
    background: url('./defense-white.svg') center no-repeat;
    background-size: contain;
}

.menu-list-button.att {
    background: url('./attack-white.svg') center no-repeat;
    background-size: contain;
}

.menu-list-button.hp {
    background: url('./heart-white.svg') center no-repeat;
    background-size: contain;
}

.menu-list-button.type {
    background: url('./type.svg') center no-repeat;
    background-size: contain;
}

.menu-list-button.asc:after {
    content: '';
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    border: 5px solid transparent;
    border-bottom: 9px solid #FFF;
    right: 0;
    bottom: 0;
}

.menu-list-button.des:after {
    content: '';
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    border: 5px solid transparent;
    border-top: 9px solid #FFF;
    right: 0;
    bottom: -5px;
}

.menu-types {
    max-width: 900px;
    margin: 0 auto;
    background: #32323a8a;
    border-radius: 5px;
    padding: 10px 0;
}

.menu_types_list {
    display: inline-block;
    padding: 5px;
    border-radius: 5px;
    min-width: 65px;
    font-size: 14px;
    text-shadow: 1px 1px 1px #000;
    cursor: pointer;
    background: #404040;
    color: #fff;
    border: 0 none;
    margin: 4px;
    text-transform: capitalize;
}

.type-selected {
    border: 0 none;
    border-radius: 5px;
    background: #404040;
    color: #fff;
    min-width: 65px;
    text-shadow: 1px 1px 1px #000;
    font-size: 28px;
    padding: 7px 10px;
    vertical-align: middle;
    text-transform: capitalize;
}